import React from 'react'
import img1 from '../image.jpg'

import { useState } from 'react'

const Header = () => {
 
  return (
    <div className='header'>
      <section className='main-content'>
        <div className='content'>
          <div className='home_content'>
            <h2 style={{ fontSize: "80px", fontWeight: "500" }}>SocioSynapse</h2>
            <h2 style={{ fontSize: "60px" }}>Build  .  Impact  .  Connect</h2>
            <h4 style={{ fontSize: "30px" }}>Reach your target audience through the voices they trust</h4>
          </div>
        </div>
      </section>
      <div className='button'>
        <a href='#contact'><button className='contact_button'>Contact Us</button></a>
      </div>
    </div>
  )
}

export default Header
