import React from 'react'
import { RiMenu3Line, RiCloseLine } from 'react-icons/ri'
import { Link } from 'react-router-dom'
import { useState } from 'react'
import logo from '../logo.png'

const Navbar = () => {
    const [toggleMenu, setToggleMenu] = useState(false)
  return (
    <div>
      <nav className="navbar py-4">
        <div className="container-fluid">
          <a className="navbar-brand"><img className='nav_image' src={logo} alt='logo'></img></a>
          <div className='navbar_links'>
          <Link to="" style={{textDecoration: "none" , color:"white"}}>
            <a>Home</a>
            </Link>
            <Link to="/about" style={{textDecoration: "none" , color:"white"}}>
            <a >About</a>
            </Link>
            <a>Blog</a>
            <a href='#contact'><button className='meet' style={{ color: "#E6C7EB"}}>Let's Meet!</button></a>
          </div>
        </div>
        <div className="gpt3__navbar-menu">
        {toggleMenu
          ? <RiCloseLine color="#fff" size={27} onClick={() => setToggleMenu(false)} />
          : <RiMenu3Line color="#fff" size={27} onClick={() => setToggleMenu(true)} />}
        {toggleMenu && (
          <div className="gpt3__navbar-menu_container">
            <div className="gpt3__navbar-menu_container-links scale-up-center">
              <p><a href="/">Home</a></p>
              <Link to="/about" style={{textDecoration: "none" , color:"white"}}>
              <p><a href="#wgpt3">About</a></p>
              </Link>
              <p><a href="#possibility">Contact</a></p>
              <p><a href="#features">Blog</a></p>
              <a href='#contact'><button className='meet_button'>Let's Meet</button></a>
            </div>
          </div>
        )}
      </div>
      </nav>
    </div>
  )
}

export default Navbar
