import React from 'react'
import contact_image from "../contact_image.jpg"
import { useState } from 'react';
import { Link } from 'react-router-dom';

const Footer = () => {
  const [status, setStatus] = useState("Submit");
  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus("Sending...");
    const { name, email,subject, message } = e.target.elements;
    let details = {
      name: name.value,
      email: email.value,
      subject: subject.value,
      message: message.value,
    };

    let response = await fetch("http://localhost:5000/contact", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(details),
    });

    setStatus("Submit");
    let result = await response.json();
    alert(result.status);
  };

  return (
    <div>
      <section className='footer'>
        <div className='contact_us' id='contact'>
            <div className='contact_image'>
                <img src={contact_image}></img>
            </div>
            <form className='form_group' onSubmit={handleSubmit}>
                <h2 style={{fontFamily: "Cabin", fontSize: "36px" , letterSpacing: "0.4rem", color:"#E6C7EB"}}>CONTACT US</h2>
                <div className='name_email'>
                    <input type="text" placeholder='Name' id='name' />
                    <input type="email" placeholder='Email' id='email' />
                </div>
                    <input type="text" placeholder='Subject' id='subject' />
                    <textarea placeholder='Type your message here...' rows={5} id='message' ></textarea>
                    <button className='submit_button'>{status}</button>
            </form>
        </div>
        <div className="socials">
          <h2>Follow us on social media!</h2>
        <a href='https://instagram.com/sociosynapse?igshid=ZDdkNTZiNTM=' target="_blank"><i class="fa-brands fa-instagram fa-2x"></i></a>
        <a href='https://www.linkedin.com/company/socio-synapse/' target="_blank"><i class="fa-brands fa-linkedin fa-2x"></i></a>
        </div>
      </section>
    </div>
  )
}

export default Footer
