import React from 'react'
import talk_image from '../talk_image.jpg'

const Home = () => {
  return (
    <div>
      <section className='talk_container'>
        <div className='talk_content'>
            <div className='lets_talk'>
                <h1 style={{fontSize: "60px" ,fontFamily: "Cabin", color: "#E6C7EB"}}>Let's talk</h1>
                <p>SocioSynapse is the go to destination for all  your influencer marketing needs. We connect you to the right creators with the most suited audience according to your needs. Whether it’s a new product launch, visual merchandising, promoting content or paid promotions we do it all. We connect you with the best suited audience for the product in the least amount of time and capital.</p>
            </div>
            <div className='talk_image'>
                <img src={talk_image}></img>
            </div>
        </div>
      </section>
    </div>
  )
}

export default Home
